import React from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { logoutUser } from '../actions/userActions';

export default function Navbar() {
  const cartstate = useSelector(state => state.cartReducer);
  const userState = useSelector(state => state.loginUserReducer)
  const { currentUser } = userState
  const dispatch = useDispatch()
  return (
    <nav className="navbar navbar-expand-lg shadow p-3 mb-5 bg-white rounded">
      <a className="navbar-brand" href="/">Order-Food</a>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarNav">
        <ul className="navbar-nav ms-auto">
          {currentUser ? (
            <div className="dropdown">
              <button className="btn btn-secondary dropdown-toggle text-black"  type="button" data-bs-toggle="dropdown" aria-expanded="false">
              {currentUser.name}
              </button>
              <ul className="dropdown-menu">
                <li><a className="dropdown-item" href="/orders">Orders</a></li>
                <li><a className="dropdown-item" href="#" onClick={() =>{dispatch(logoutUser())}}>LogOut</a></li>
              </ul>
            </div>) : (
            <li className="nav-item active">
              <a className="nav-link" href="/login">Login </a>
            </li>)}
          <li className="nav-item">
            <a className="nav-link" href="/cart"> Cart {cartstate.cartItems.length}</a>
          </li>
        </ul>
      </div>
    </nav>
  )
}

